// Pobierz element canvas i kontekst WebGL
const canvas = document.querySelector("#particles-canvas");
const gl = canvas.getContext("webgl");

// Sprawd≈∫, czy WebGL jest wspierany
if (!gl) {
    alert("WebGL nie jest wspierany przez TwojƒÖ przeglƒÖdarkƒô.");
}

// Ustawienie rozmiar√≥w canvas na ca≈Çy ekran
canvas.width = window.innerWidth;
canvas.height = window.innerHeight;
gl.viewport(0, 0, canvas.width, canvas.height);

// Konfigurowalne parametry
const config = {
    particleCount: 5000, // Liczba czƒÖsteczek
    textArray: ["UltraPower", "Super Moce"], // Tablica tekst√≥w do wy≈õwietlenia
    mouseRadius: 0.1, // Promie≈Ñ interakcji myszy
    particleSize: 2, // Rozmiar czƒÖsteczek
    forceMultiplier: 0.001, // Mno≈ºnik si≈Çy odpychania
    returnSpeed: 0.005, // Prƒôdko≈õƒá powrotu czƒÖsteczek do pozycji bazowej
    velocityDamping: 0.95, // T≈Çumienie prƒôdko≈õci czƒÖsteczek
    colorMultiplier: 40000, // Mno≈ºnik koloru (wp≈Çywa na hue)
    saturationMultiplier: 1000, // Mno≈ºnik nasycenia koloru
    textChangeInterval: 10000, // Interwa≈Ç zmiany tekstu (ms)
    rotationForceMultiplier: 0.5 // Mno≈ºnik si≈Çy rotacji
};

// Inicjalizacja indeksu aktualnego tekstu
let currentTextIndex = 0;
// Zmienna dla timeoutu zmiany tekstu
let nextTextTimeout;
// Tablica wsp√≥≈Çrzƒôdnych tekstu
let textCoordinates = [];

// Obiekt reprezentujƒÖcy mysz
const mouse = {
    x: -500, // Pozycja X myszy (poza ekranem na poczƒÖtku)
    y: -500, // Pozycja Y myszy (poza ekranem na poczƒÖtku)
    radius: config.mouseRadius // Promie≈Ñ interakcji myszy
};

// Tablica czƒÖsteczek
const particles = [];
// Inicjalizacja czƒÖsteczek
for (let i = 0; i < config.particleCount; i++) {
    particles.push({ x: 0, y: 0, baseX: 0, baseY: 0, vx: 0, vy: 0 });
}

// Vertex shader - odpowiedzialny za pozycjƒô i rozmiar czƒÖsteczek
const vertexShaderSource = `
    attribute vec2 a_position; // Atrybut - pozycja czƒÖsteczki
    attribute float a_hue; // Atrybut - hue (odcie≈Ñ) koloru
    attribute float a_saturation; // Atrybut - nasycenie koloru
    varying float v_hue; // Zmienna wariacyjna - hue przekazywane do fragment shader'a
    varying float v_saturation; // Zmienna wariacyjna - nasycenie przekazywane do fragment shader'a
    void main() {
        gl_PointSize = ${config.particleSize.toFixed(1)}; // Ustawienie rozmiaru punktu (czƒÖsteczki)
        gl_Position = vec4(a_position, 0.0, 1.0); // Ustawienie pozycji czƒÖsteczki
        v_hue = a_hue; // Przekazanie hue do fragment shader'a
        v_saturation = a_saturation; // Przekazanie nasycenia do fragment shader'a
    }
`;

// Fragment shader - odpowiedzialny za kolor czƒÖsteczek
const fragmentShaderSource = `
    precision mediump float; // Ustawienie precyzji na ≈õredniƒÖ
    varying float v_hue; // Zmienna wariacyjna - hue otrzymane z vertex shader'a
    varying float v_saturation; // Zmienna wariacyjna - nasycenie otrzymane z vertex shader'a
    void main() {
        // Definicje kolor√≥w (zielony i fioletowy)
        vec3 green = vec3(0.0, 1.0, 0.62); // #00FF9F - zielony
        vec3 purple = vec3(0.61, 0.0, 1.0); // #9D00FF - fioletowy

        // Mieszanie kolor√≥w w zale≈ºno≈õci od v_hue (prƒôdko≈õci)
        float mixValue = mod(v_hue, 1.0); // U≈ºywamy mod, aby warto≈õci by≈Çy miƒôdzy 0 a 1
        vec3 color = mix(green, purple, mixValue); // Mieszanie zielonego i fioletowego

        // Mieszanie koloru z bia≈Çym w zale≈ºno≈õci od nasycenia
        vec3 finalColor = mix(vec3(1.0), color, v_saturation);
        gl_FragColor = vec4(finalColor, 1.0); // Ustawienie koloru fragmentu
    }
`;

// Funkcja do tworzenia shader'a
function createShader(gl, type, source) {
    const shader = gl.createShader(type); // Utworzenie shader'a danego typu
    gl.shaderSource(shader, source); // Ustawienie ≈∫r√≥d≈Ça shader'a
    gl.compileShader(shader); // Kompilacja shader'a
    // Sprawdzenie statusu kompilacji
    if (!gl.getShaderParameter(shader, gl.COMPILE_STATUS)) {
        console.error(gl.getShaderInfoLog(shader)); // Wy≈õwietlenie logu b≈Çƒôd√≥w kompilacji
        gl.deleteShader(shader); // Usuniƒôcie shader'a w przypadku b≈Çƒôdu
        return null; // Zwr√≥cenie null
    }
    return shader; // Zwr√≥cenie skompilowanego shader'a
}

// Funkcja do tworzenia programu shader'owego
function createProgram(gl, vertexShader, fragmentShader) {
    const program = gl.createProgram(); // Utworzenie programu shader'owego
    gl.attachShader(program, vertexShader); // Do≈ÇƒÖczenie vertex shader'a
    gl.attachShader(program, fragmentShader); // Do≈ÇƒÖczenie fragment shader'a
    gl.linkProgram(program); // Linkowanie programu
    // Sprawdzenie statusu linkowania
    if (!gl.getProgramParameter(program, gl.LINK_STATUS)) {
        console.error(gl.getProgramInfoLog(program)); // Wy≈õwietlenie logu b≈Çƒôd√≥w linkowania
        gl.deleteProgram(program); // Usuniƒôcie programu w przypadku b≈Çƒôdu
        return null; // Zwr√≥cenie null
    }
    return program; // Zwr√≥cenie zlinkowanego programu
}

// Utworzenie vertex shader'a
const vertexShader = createShader(gl, gl.VERTEX_SHADER, vertexShaderSource);
// Utworzenie fragment shader'a
const fragmentShader = createShader(
    gl,
    gl.FRAGMENT_SHADER,
    fragmentShaderSource
);
// Utworzenie programu shader'owego
const program = createProgram(gl, vertexShader, fragmentShader);

// Pobranie lokalizacji atrybut√≥w w programie shader'owym
const positionAttributeLocation = gl.getAttribLocation(program, "a_position");
const hueAttributeLocation = gl.getAttribLocation(program, "a_hue");
const saturationAttributeLocation = gl.getAttribLocation(
    program,
    "a_saturation"
);

// Utworzenie bufor√≥w dla pozycji, hue i nasycenia
const positionBuffer = gl.createBuffer();
const hueBuffer = gl.createBuffer();
const saturationBuffer = gl.createBuffer();

// Utworzenie tablic dla danych pozycji, hue i nasycenia
const positions = new Float32Array(config.particleCount * 2);
const hues = new Float32Array(config.particleCount);
const saturations = new Float32Array(config.particleCount);

// Funkcja do pobierania wsp√≥≈Çrzƒôdnych tekstu na canvas
function getTextCoordinates(text) {
    const ctx = document.createElement("canvas").getContext("2d"); // Utworzenie tymczasowego canvas 2D
    ctx.canvas.width = canvas.width; // Ustawienie szeroko≈õci canvas
    ctx.canvas.height = canvas.height; // Ustawienie wysoko≈õci canvas
    const fontSize = Math.min(canvas.width / 6, canvas.height / 6); // Obliczenie rozmiaru czcionki
    ctx.font = `900 ${fontSize}px Arial`; // Ustawienie czcionki
    ctx.fillStyle = "white"; // Ustawienie koloru tekstu na bia≈Çy
    ctx.textAlign = "center"; // Ustawienie wyr√≥wnania tekstu na ≈õrodek
    ctx.textBaseline = "middle"; // Ustawienie linii bazowej tekstu na ≈õrodek
    ctx.fillText(text, canvas.width / 2, canvas.height / 2); // Wype≈Çnienie tekstem na canvas
    const imageData = ctx.getImageData(0, 0, canvas.width, canvas.height).data; // Pobranie danych obrazu z canvas
    const coordinates = []; // Tablica na wsp√≥≈Çrzƒôdne
    // Iteracja po danych obrazu co 4 piksele (R, G, B, A)
    for (let y = 0; y < canvas.height; y += 4) {
        for (let x = 0; x < canvas.width; x += 4) {
            const index = (y * canvas.width + x) * 4; // Obliczenie indeksu piksela
            // Sprawdzenie, czy kana≈Ç alfa jest wiƒôkszy ni≈º 128 (pr√≥g przezroczysto≈õci)
            if (imageData[index + 3] > 128) {
                coordinates.push({
                    x: (x / canvas.width) * 2 - 1, // Normalizacja wsp√≥≈Çrzƒôdnej X do zakresu -1 do 1
                    y: (y / canvas.height) * -2 + 1 // Normalizacja wsp√≥≈Çrzƒôdnej Y do zakresu -1 do 1
                });
            }
        }
    }
    return coordinates; // Zwr√≥cenie tablicy wsp√≥≈Çrzƒôdnych
}

// Funkcja do tworzenia czƒÖsteczek na podstawie tekstu
function createParticles() {
    textCoordinates = getTextCoordinates(config.textArray[currentTextIndex]); // Pobranie wsp√≥≈Çrzƒôdnych tekstu
    // Ustawienie pozycji bazowych czƒÖsteczek na wsp√≥≈Çrzƒôdne tekstu
    for (let i = 0; i < config.particleCount; i++) {
        const randomIndex = Math.floor(Math.random() * textCoordinates.length); // Losowy indeks wsp√≥≈Çrzƒôdnej
        const { x, y } = textCoordinates[randomIndex]; // Pobranie losowej wsp√≥≈Çrzƒôdnej
        particles[i].x = particles[i].baseX = x; // Ustawienie pozycji X i bazowej pozycji X
        particles[i].y = particles[i].baseY = y; // Ustawienie pozycji Y i bazowej pozycji Y
    }
}

// Funkcja do aktualizacji pozycji czƒÖsteczek
function updateParticles() {
    // Iteracja po wszystkich czƒÖsteczkach
    for (let i = 0; i < config.particleCount; i++) {
        const particle = particles[i]; // Pobranie czƒÖsteczki
        const dx = mouse.x - particle.x; // Obliczenie r√≥≈ºnicy X miƒôdzy myszƒÖ a czƒÖsteczkƒÖ
        const dy = mouse.y - particle.y; // Obliczenie r√≥≈ºnicy Y miƒôdzy myszƒÖ a czƒÖsteczkƒÖ
        const distance = Math.sqrt(dx * dx + dy * dy); // Obliczenie odleg≈Ço≈õci miƒôdzy myszƒÖ a czƒÖsteczkƒÖ
        const forceDirectionX = dx / distance; // Kierunek si≈Çy w osi X
        const forceDirectionY = dy / distance; // Kierunek si≈Çy w osi Y
        const maxDistance = mouse.radius; // Maksymalna odleg≈Ço≈õƒá interakcji myszy
        const force = (maxDistance - distance) / maxDistance; // Si≈Ça odpychania (maleje z odleg≈Ço≈õciƒÖ)
        const directionX = forceDirectionX * force * config.forceMultiplier; // Sk≈Çadowa X si≈Çy
        const directionY = forceDirectionY * force * config.forceMultiplier; // Sk≈Çadowa Y si≈Çy
        const angle = Math.atan2(dy, dx); // KƒÖt miƒôdzy czƒÖsteczkƒÖ a myszƒÖ

        // Si≈Ça rotacji czƒÖsteczek wok√≥≈Ç myszy
        const rotationForceX = Math.sin(
            -Math.cos(angle * -1) *
            Math.sin(config.rotationForceMultiplier * Math.cos(force)) *
            Math.sin(distance * distance) *
            Math.sin(angle * distance)
        );
        const rotationForceY = Math.sin(
            Math.cos(angle * 1) *
            Math.sin(config.rotationForceMultiplier * Math.sin(force)) *
            Math.sin(distance * distance) *
            Math.cos(angle * distance)
        );

        // Je≈õli czƒÖsteczka jest blisko myszy, odepchnij jƒÖ
        if (distance < mouse.radius) {
            particle.vx -= directionX + rotationForceX; // Zmiana prƒôdko≈õci X
            particle.vy -= directionY + rotationForceY; // Zmiana prƒôdko≈õci Y
        } else {
            // W przeciwnym razie, przyciƒÖgaj czƒÖsteczkƒô do pozycji bazowej
            particle.vx += (particle.baseX - particle.x) * config.returnSpeed; // Powr√≥t do pozycji bazowej X
            particle.vy += (particle.baseY - particle.y) * config.returnSpeed; // Powr√≥t do pozycji bazowej Y
        }

        // Aktualizacja pozycji czƒÖsteczki
        particle.x += particle.vx;
        particle.y += particle.vy;
        // T≈Çumienie prƒôdko≈õci czƒÖsteczek
        particle.vx *= config.velocityDamping;
        particle.vy *= config.velocityDamping;
        // Obliczenie prƒôdko≈õci i hue na podstawie prƒôdko≈õci
        const speed = Math.sqrt(
            particle.vx * particle.vx + particle.vy * particle.vy
        );
        const hue = (speed * config.colorMultiplier) % 360; // Hue zale≈ºy od prƒôdko≈õci

        // Ustawienie hue i nasycenia dla czƒÖsteczki
        hues[i] = hue / 360; // Normalizacja hue do zakresu 0-1
        saturations[i] = Math.min(speed * config.saturationMultiplier, 1); // Nasycenie zale≈ºy od prƒôdko≈õci, max 1
        positions[i * 2] = particle.x; // Ustawienie pozycji X w tablicy pozycji
        positions[i * 2 + 1] = particle.y; // Ustawienie pozycji Y w tablicy pozycji
    }
    // Aktualizacja bufor√≥w pozycji, hue i nasycenia
    gl.bindBuffer(gl.ARRAY_BUFFER, positionBuffer);
    gl.bufferData(gl.ARRAY_BUFFER, positions, gl.DYNAMIC_DRAW); // Dynamiczne rysowanie - dane mogƒÖ siƒô zmieniaƒá
    gl.bindBuffer(gl.ARRAY_BUFFER, hueBuffer);
    gl.bufferData(gl.ARRAY_BUFFER, hues, gl.DYNAMIC_DRAW); // Dynamiczne rysowanie - dane mogƒÖ siƒô zmieniaƒá
    gl.bindBuffer(gl.ARRAY_BUFFER, saturationBuffer);
    gl.bufferData(gl.ARRAY_BUFFER, saturations, gl.DYNAMIC_DRAW); // Dynamiczne rysowanie - dane mogƒÖ siƒô zmieniaƒá
}

// Funkcja animacji
function animate() {
    updateParticles(); // Aktualizacja pozycji czƒÖsteczek
    gl.clear(gl.COLOR_BUFFER_BIT); // Czyszczenie canvas
    // Ustawienie atrybut√≥w pozycji, hue i nasycenia
    gl.bindBuffer(gl.ARRAY_BUFFER, positionBuffer);
    gl.vertexAttribPointer(positionAttributeLocation, 2, gl.FLOAT, false, 0, 0); // Ustawienie wska≈∫nika na dane pozycji
    gl.enableVertexAttribArray(positionAttributeLocation); // W≈ÇƒÖczenie atrybutu pozycji
    gl.bindBuffer(gl.ARRAY_BUFFER, hueBuffer);
    gl.vertexAttribPointer(hueAttributeLocation, 1, gl.FLOAT, false, 0, 0); // Ustawienie wska≈∫nika na dane hue
    gl.enableVertexAttribArray(hueAttributeLocation); // W≈ÇƒÖczenie atrybutu hue
    gl.bindBuffer(gl.ARRAY_BUFFER, saturationBuffer);
    gl.vertexAttribPointer(saturationAttributeLocation, 1, gl.FLOAT, false, 0, 0); // Ustawienie wska≈∫nika na dane nasycenia
    gl.enableVertexAttribArray(saturationAttributeLocation); // W≈ÇƒÖczenie atrybutu nasycenia
    gl.useProgram(program); // U≈ºycie programu shader'owego
    gl.drawArrays(gl.POINTS, 0, config.particleCount); // Rysowanie punkt√≥w (czƒÖsteczek)
    requestAnimationFrame(animate); // Wywo≈Çanie animacji w nastƒôpnej klatce
}

// Detektor zdarze≈Ñ dla ruchu myszy
canvas.addEventListener("mousemove", (event) => {
    mouse.x = (event.clientX / canvas.width) * 2 - 1; // Normalizacja pozycji X myszy
    mouse.y = (event.clientY / canvas.height) * -2 + 1; // Normalizacja pozycji Y myszy
});

// Detektor zdarze≈Ñ dla opuszczenia canvas przez mysz
canvas.addEventListener("mouseleave", () => {
    mouse.x = -500; // Ustawienie pozycji X myszy poza ekran
    mouse.y = -500; // Ustawienie pozycji Y myszy poza ekran
});

// Detektor zdarze≈Ñ dla zmiany rozmiaru okna
window.addEventListener("resize", () => {
    canvas.width = window.innerWidth; // Aktualizacja szeroko≈õci canvas
    canvas.height = window.innerHeight; // Aktualizacja wysoko≈õci canvas
    gl.viewport(0, 0, canvas.width, canvas.height); // Aktualizacja viewport WebGL
    createParticles(); // Ponowne utworzenie czƒÖsteczek dla nowego rozmiaru
});

// Funkcja do zmiany tekstu
function changeText() {
    currentTextIndex = (currentTextIndex + 1) % config.textArray.length; // Przej≈õcie do nastƒôpnego tekstu w tablicy
    const newCoordinates = getTextCoordinates(config.textArray[currentTextIndex]); // Pobranie wsp√≥≈Çrzƒôdnych nowego tekstu
    // Aktualizacja pozycji bazowych czƒÖsteczek na nowe wsp√≥≈Çrzƒôdne tekstu
    for (let i = 0; i < config.particleCount; i++) {
        const randomIndex = Math.floor(Math.random() * newCoordinates.length); // Losowy indeks wsp√≥≈Çrzƒôdnej
        const { x, y } = newCoordinates[randomIndex]; // Pobranie losowej wsp√≥≈Çrzƒôdnej
        particles[i].baseX = x; // Ustawienie bazowej pozycji X
        particles[i].baseY = y; // Ustawienie bazowej pozycji Y
    }
    nextTextTimeout = setTimeout(changeText, config.textChangeInterval); // Ustawienie timeoutu dla nastƒôpnej zmiany tekstu
}

// Ustawienie koloru czyszczenia canvas na czarny
gl.clearColor(0, 0, 0, 1);
// Utworzenie czƒÖsteczek
createParticles();
// Rozpoczƒôcie animacji
animate();
// Uruchomienie zmiany tekstu po pierwszym interwale
nextTextTimeout = setTimeout(changeText, config.textChangeInterval);
